body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.form-wrapper{
  text-align: center;
    margin: auto;
    width: 25%;
}
label{
  float: left;
}
.form-elemant{
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  outline: 0;
  padding: 5px;
}
.btn-submit{
  margin: 15px 15px 0 0px;
    border: 0;
    border-radius: 4px;
    color: #fff;
    background: green;
    padding: 10px;
}
.login{
  font-size: 12px;
    font-weight: 600;
    color: dodgerblue;
}
span{
  font-size: 12px;
}
.nav-wrapper{
  background: linear-gradient(10deg, black, red);
  display: -webkit-flex;
  display: flex;
}
.right {
  display: -webkit-flex;
  display: flex;
  margin: 0 52px;
  width: 75%;
}
.right div{
  -webkit-order: 1;
          order: 1;
  padding: 10px;
}
.left{
  display: -webkit-flex;
  display: flex;
}
.left div {
  padding: 10px;
  color: #fff;
  cursor: pointer;
}
a{
  text-decoration: none;
  color: #fff
}
.task{
  text-align: center;
    margin: auto;
    width: 70%;
}
.task-wrapper{
  display: -webkit-flex;
  display: flex;
  border: 1px solid #ddd;
  width: 100%;
}
.task-container{
  width: 18%;
  border-right: 1px solid #ddd;
  padding: 5px;
}
.react-datepicker-wrapper{
  width: 100%;
}
.time-container{
  width: 100%;
  display: -webkit-flex;
  display: flex;
}
.time-container span{
  font-weight: 700;
  margin-top: 4px;
}
.time-elemant {
  width: 8%;
  border: 1px solid #ddd;
  border-radius: 4px;
  outline: 0;
  padding: 5px;
  margin: 0 4px;
}
.create-task{
    position: absolute;
    right: 100px;
    bottom: 100px;
    color: #fff;
    background: #55b668;
    border-radius: 55%;
    padding: 0px 11px 5px 11px;
}
i {
  font-size: 30px;
  font-weight: 700;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

